.container{
    width: 607px;
    height: 829px;
    margin: auto;
    margin-top: 75px;
    border-radius: 12px;
    background-color: violet;
}
.top-bar{
    display: flex;
    justify-content: center;
    gap: 14px;
    padding: 60px;
}
.top-bar input{
    display: flex;

    width: 362px;
    height: 78px;
    background-color: aliceblue;
    border: none;
    outline:  none;
    border-radius: 40px;
    padding-left: 40px;
    color:rgb(198, 104, 36);
    font-size: 20px;
    font-weight: 400;
}
.search-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 78px;
    height: 78px;
    background-color: aliceblue;
    border-radius: 40px;
    cursor: pointer;
}
.weather-img{
    margin-top:29px;
    display: flex;
    justify-content: center;
}
.weather-temp{
    display: flex;
    justify-content: center;
    font-size: 120px;
    color:white;
    font-weight: 400;
}
.weather-location{
    color: white;
    display: flex;
    font-size: 60px;
    justify-content: center;
    font-weight: 400;

}
.datacontainer{
margin-top: 50px;
color: white;
display: flex;
justify-content: center;
}

.element{
    margin: auto;
    display: flex;
    align-items: flex-start;
gap: 12px;

}

.data{
    font-size: 34px;
    font-weight: 400;

}
.text{
    font-size: 20px;
    font-weight: 400;
}
.icon{
    margin-top: 10px;
}